* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  font-size: 6px;
  background-color: var(--background-color);
  color: var(--text-color);
  transition: background-color 0.3s, color 0.3s;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: #333;
  font-family: 'Poppins', sans-serif;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2px;
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
}

.navbar .theme-switch {
  cursor: pointer;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 1px 2px;
  margin: 1px;
  border: none;
  background-color: var(--button-bg-color);
  color: var(--button-text-color);
  border-radius: 2px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: var(--button-hover-bg-color);
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 2px;
}

.table th, .table td {
  padding: 1px;
  text-align: left;
  border-bottom: 1px solid var(--table-border-color);
}

:root {
  --background-color: #ffffff;
  --text-color: #333333;
  --button-bg-color: #f0f0f0;
  --button-text-color: #333333;
  --button-hover-bg-color: #e0e0e0;
  --table-border-color: #dddddd;
}

[data-theme='dark'] {
  --background-color: #1e1e1e;
  --text-color: #f0f0f0;
  --button-bg-color: #333333;
  --button-text-color: #f0f0f0;
  --button-hover-bg-color: #444444;
  --table-border-color: #444444;
}

/* Стили слайдера */
.slider-container {
  position: relative;
  width: 100%;
  margin-bottom: 2px;
}

.slider {
  position: relative;
  width: 100%;
  height: 100px;
  overflow: hidden;
  border-radius: 2px;
}

.slider-wrapper {
  display: flex;
  transition: transform 0.3s ease;
}

.slider-wrapper img {
  width: 100%;
  height: 100px;
  object-fit: cover;
}

.default-slide {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f0f0f0;
}

.slider-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0,0,0,0.5);
  border: none;
  color: white;
  padding: 2px;
  cursor: pointer;
}

.slider-btn.prev { left: 2px; }
.slider-btn.next { right: 2px; }

.slider-controls {
  position: absolute;
  bottom: 2px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 2px;
}

/* Стили для блока ингредиентов */
.ingredients-container {
  margin-bottom: 2px;
}

.ingredients-header {
  display: flex;
  padding: 1px;
  background: #f0f0f0;
  margin-bottom: 2px;
}

.ingredient-name { width: 50%; }
.ingredient-weight,
.ingredient-waste { 
  display: block;
  left: 2px;
  width: 10%; }

.ingredient-row {
  display: flex;
  padding: 1px 2px;
  border-bottom: 1px solid #ddd;
}

.add-ingredient {
  margin-top: 2px;
  padding: 2px;
  background: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

/* Стили для вкладок */
.tabs-container {
  margin-top: 2px;
}

.tabs {
  display: flex;
  margin-bottom: 2px;
}

.tab-btn {
  flex: 1;
  padding: 2px;
  border: none;
  background: #f0f0f0;
  cursor: pointer;
}

.tab-btn.active {
  background: #ddd;
}

.tab-content {
  display: none;
}

.tab-content.active {
  display: block;
}

/* Модальные окна */
.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}

.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 2px;
  border-radius: 2px;
}

.modal-content input,
.modal-content select,
.modal-content textarea {
  width: 100%;
  margin-bottom: 2px;
  padding: 1px;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
}

.modal-buttons button {
  margin-left: 1px;
  padding: 1px 2px;
  cursor: pointer;
}

/* Стили для видео галереи */
.video-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 2px;
}

.video-item {
  position: relative;
}

.video-item video {
  width: 100%;
  height: auto;
}

.remove-video {
  position: absolute;
  top: 1px;
  right: 1px;
  background: rgba(255,0,0,0.7);
  color: white;
  border: none;
  padding: 1px;
  cursor: pointer;
}

.steps-list {
  display: block;
  margin-top: 2px;
}

.header-controls {
  margin-top: 2px;
}

#search-input {
  padding: 2px;
  width: 60%;
  max-width: 150px;
}

#add-recipe-btn {
  padding: 2px 4px;
  background-color: #45a049;
  color: white;
  border: none;
  cursor: pointer;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 2px;
  border-radius: 2px;
  width: 90%;
  max-width: 250px;
}

.modal
{

  padding: 1px;
  background: #f8f8f8;
  overflow-x: auto;
}

.tab-btn {
  padding: 1px 2px;
  font-size: 6px;
  border: none;
  border-radius: 2px;
  background: #eee;
  white-space: nowrap;
}

.tab-btn.active {
  background: #4CAF50;
  color: white;
}

/* Склад */
.warehouse {
  padding: 1px;
}

.filter-input {
  width: 100%;
  padding: 1px;
  margin: 1px 0;
  border: 1px solid #ddd;
  border-radius: 2px;
  font-size: 6px;
  box-sizing: border-box;
}

.category {
  margin: 1px 0;
  border: 1px solid #eee;
  border-radius: 2px;
}

.category-header {
  padding: 1px;
  background: #f8f8f8;
  display: flex;
  align-items: center;
  gap: 1px;
}

.category-header h2 {
  margin: 0;
  font-size: 6px;
}

.ingredients-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 6px;
}

.ingredients-table th,
.ingredients-table td {
  padding: 1px;
  text-align: left;
  border-bottom: 1px solid #eee;
}

/* Общие стили для текста */
h1 {
  font-size: 8px;
  margin: 2px 1px;
}

h2 {
  font-size: 6px;
  margin: 2px 0;
}

/* Адаптивность для очень маленьких экранов */
@media (max-width: 320px) {
  body {
    font-size: 6px;
  }

  .ingredient-name { width: 30%; }
  .ingredient-brutto,
  .ingredient-netto,
  .ingredient-waste,
  .ingredient-ready { 
    width: 17.5%; 
    font-size: 6px;
  }

  .modal-content {
    padding: 1px;
  }

  .modal-buttons button {
    padding: 1px;
    font-size: 6px;
  }
}
.add-step{
  margin-left: 1px;
}

