.ingredient-name {
    font-size: 10px;
    min-width: 50%; }
.ingredient-brutto,
.ingredient-netto,
.ingredient-waste,
.ingredient-ready,
.ingredient-cost {
    font-size: 8px;
    }

.ingredient-cost {
  margin-left: 25px;
  color: #2196F3;
  font-weight: bold;
}
.ingredients-container {
  margin-bottom: 20px;
}

.ingredients-header {
  display: flex;
  font-weight: bold;
  background-color: #f0f0f0;
  padding: 10px 0;
}

.ingredients-list {
  max-height: 300px;
  overflow-y: auto;
}

.ingredient-row {
  display: flex;
  padding: 5px 0;
  border-bottom: 1px solid #eee;
}

.ingredient-row:hover {
  background-color: #f9f9f9;
}

.ingredient-name,
.ingredient-brutto,
.ingredient-netto,
.ingredient-waste,
.ingredient-ready,
.ingredient-cost {
  flex: 1;
  padding: 0 5px;
  text-align: center;
}

.ingredient-name {
  flex: 2;
  text-align: left;
}

.add-ingredient {
  margin-top: 10px;
  padding: 5px 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 16px;
}

.add-ingredient:hover {
  background-color: #45a049;
}

.ingredients-totals {
  gap: 20px;
  display: flex;
  margin-top: 10px;
  padding: 10px;
  border-top: 1px solid #ddd;
justify-content: flex-end;
}

.total-row {
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
  font-size: 14px;
  font-weight: bold;
}