.dashboard {
  padding: 2px;
}

.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2px;
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
}

.user-profile {
  display: flex;
  align-items: center;
}

.user-profile img {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 2px;
}

.user-profile h2 {
  font-size: 8px;
  margin: 0;
}

.user-profile p {
  font-size: 6px;
  margin: 0;
}

.logout-btn {
  background-color: #e0e0e0;
  border: none;
  border-radius: 4px;
  padding: 4px 8px;
  font-size: 8px;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.logout-btn:hover {
  background-color: #bdbdbd;
}

.dashboard-nav {
  display: flex;
  justify-content: space-between;
  padding: 2px;
  position: relative;
}

.nav-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  color: #333;
  font-size: 10px; /* Увеличенный шрифт */
  padding: 4px;
  position: relative;
  z-index: 1;
}

.nav-link i {
  font-size: 16px; /* Увеличенный шрифт для иконок */
  margin-bottom: 2px;
}

.nav-link.active {
  color: #fff;
}

.nav-link.active::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #4CAF50;
  border-radius: 4px;
  z-index: -1;
  animation: glow 1s ease-in-out infinite alternate;
}

@keyframes glow {
  from {
    box-shadow: 0 0 5px #4CAF50;
  }
  to {
    box-shadow: 0 0 20px #4CAF50;
  }
}

.nav-link.active::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #4CAF50;
  border-radius: 4px;
  z-index: -2;
  animation: pulse 1s ease-in-out infinite alternate;
}

@keyframes pulse {
  from {
    transform: scale(1);
    opacity: 0.5;
  }
  to {
    transform: scale(1.2);
    opacity: 0;
  }
}

.dashboard-content {
  padding: 2px;
}
